<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section">
        <label class="slds-form-element__label cursor-label" @click="toggleEnable()">Enable Person Account</label>
        <div class="multiple-info">
          <div class="person-account-enabled-btn" :class="{'person-account-disabled-btn': !personAccountSettings.IsPersonAccountEnabled}" @click="toggleEnable()">
            <span class="slds-icon_container check-icon-span" v-if="personAccountSettings.IsPersonAccountEnabled">
              <icon name="check" icon-class-name="slds-icon slds-icon_xx-small"></icon>
            </span>
            <div :style="{'background-color': personAccountSettings.IsSupportPersonAccount ? '#fff' : '#dddbda'}" id="animate-div">
            </div>
          </div>
          <div class="person-account-enabled-info">
            {{ personAccountSettings.IsPersonAccountEnabled ? 'Enabled' : 'Disabled' }}
          </div>
        </div>
      </div>
    </div>
  </right-page>
</template>

<script>
import { getPersonAccountSettings, savePersonAccountSettings } from '../api/charket-settings'

export default {
  name: 'PersonAccountSettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Person Account Settings'
      },
      personAccountSettings: {
        IsSupportPersonAccount: false,
        IsPersonAccountEnabled: false
      }
    }
  },
  async created () {
    await this.fetchPersonAccountSettings()
    const animateDiv = document.querySelector('#animate-div')
    animateDiv.className = this.personAccountSettings.IsPersonAccountEnabled ? 'is-multiple' : ''
  },
  methods: {
    toggleEnable () {
      if (!this.isSaving && this.personAccountSettings.IsSupportPersonAccount) {
        this.personAccountSettings.IsPersonAccountEnabled = !this.personAccountSettings.IsPersonAccountEnabled
        const animateDiv = document.querySelector('#animate-div')
        animateDiv.className = this.personAccountSettings.IsPersonAccountEnabled ? 'person-account-enabled' : 'person-account-disabled'
        this.savePersonAccountSettings()
      }
    },
    async fetchPersonAccountSettings () {
      try {
        this.$bus.emit('show-loading')
        const data = await getPersonAccountSettings()
        this.personAccountSettings.IsPersonAccountEnabled = data.IsPersonAccountEnabled
        this.personAccountSettings.IsSupportPersonAccount = data.IsSupportPersonAccount
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    async savePersonAccountSettings () {
      try {
        this.isSaving = true
        await savePersonAccountSettings(JSON.stringify(this.personAccountSettings))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .section {
    padding: 20px 30px;
    .cursor-label {
      cursor: pointer;
    }
    .multiple-info {
      display: inline-block;
      vertical-align: middle;
      .person-account-enabled-btn {
        margin-top: 8px;
        width: 48px;
        height: 24px;
        background-color: #0070D2;
        border-radius: 12px;
        position: relative;
        cursor: pointer;
        .check-icon-span {
          position: absolute;
          left: 6px;
          top: 4px;
        }
        div {
          width: 20px;
          height: 20px;
          border-radius: 10px;
          background: #fff;
          position: absolute;
          top: 2px;
          left: 2px;
          cursor: pointer;
        }
        .is-multiple {
          left: 26px;
        }
      }
      .person-account-enabled-info {
        font-size: 10px;
        color: #706E6B;
      }
      .person-account-disabled-btn {
        background-color: #7e8c99;
      }
      .person-account-enabled {
        animation: bounce-in .2s linear;
        animation-fill-mode: forwards;
      }
      @keyframes bounce-in {
        0% {
          left: 2px;
        }
        50% {
          left: 14px;
        }
        100% {
          left: 26px;
        }
      }

      .person-account-disabled {
        animation: bounce-out .2s linear;
        animation-fill-mode: forwards;
      }
      @keyframes bounce-out {
        0% {
          left: 26px;
        }
        50% {
          left: 14px;
        }
        100% {
          left: 2px;
        }
      }
    }
  }
</style>
